
<template>
    <span>
        <button type="button" class="btn btn-default" @click.stop="clickSave"></button>
        <button type="button" class="btn" @click="goBack"></button>
    </span>
</template>
<script>
/*
 * 
 */
export default {
    methods: {
        'clickSave': function () {
            eventHub.$emit('do-save');
        }
        ,'goBack': function () {
            eventHub.$emit('go-back');
        }
    }
}
</script>


