<style lang="scss">
    @import "./form-group.scss";
</style>
<template>
    <input type="text" :name="qName" class="date-input form-control" v-model="inputV" :class="qClass"  @input="onInput"/>
</template>
<script>
/*
 * H5
 * param: {key,value, qClass()}
 */
    export default {
        props: ['qName', 'value', 'qClass']
        ,data () {
            return {'inputV': ''}
        }
        ,methods: {
            onInput: function (e) {
                console.log(e)
                e = e || event;
                this.$emit('input', e.target.value)
            }
        }
        ,mounted() {
            $('.date-input').datetimepicker({
                minView: 2, 
                weekStart:1,
            format: "yyyy-mm-dd", // 
            autoclose:true //
            });
        }
    }
</script>


