<style lang="scss">
    @import "./modal.scss";

</style>
<template>
    <div class="modal fade in" tabindex="-1" :id="id" role="dialog" aria-labelledby="bor-addLabel" aria-hidden="true">
        <div class="modal-dialog" :style="{width: modalW ? modalW : '600px'}" :id="'content-' + id">
            <slot></slot>
        </div>
    </div>
</template>
<script>
/*
 * Modal ,contentdom
 * param: 'id()', modalW ()
 */
    export default({
        props: ['id', 'modalW']

    });
</script>




