/**
 * @author xuld
 */

/**
 * 
 * @remark 10,000,000,000,000.00
 */
export default {
    /**
	 * 
	 * @param {Number} x 
	 * @param {Number} y 
	 * @returns {Number} 
	 * @example Currency.add(86.24, .1)
	 */
    add: function (x, y) {
        var m = Math.pow(10, Math.max(x.toString().replace(/^\d+(\.|$)/, "").length, y.toString().replace(/^\d+(\.|$)/, "").length) + 1);
        return (x * m + y * m) / m;
    },

    /**
	 * 
	 * @param {Number} x 
	 * @param {Number} y 
	 * @returns {Number} 
	 * @example Currency.sub(86.24, .1)
	 */
    sub: function (x, y) {
        var m = Math.pow(10, Math.max(x.toString().replace(/^\d+(\.|$)/, "").length, y.toString().replace(/^\d+(\.|$)/, "").length) + 1);
        return (x * m - y * m) / m;
    },

    /**
	 * 
	 * @param {Number} x 
	 * @param {Number} y 
	 * @returns {Number} 
	 * @example Currency.mul(7, 0.8)
	 */
    mul: function (x, y) {
        return (x * 1000) * (y * 1000) / 1000000;
    },

    /**
	 * 
	 * @param {Number} x 
	 * @param {Number} y 
	 * @returns {Number} 
	 * @example Currency.div(86.24, .1)
	 */
    div: function (x, y) {
        return Math.round(x * 100 / y) / 100;
    },

    /**
	 * 
	 * @param {Number} val 
	 * @returns {Number} 
	 * @example Currency.round(86.245) // 86.25
	 */
    round: function (val) {
        return Math.round(val * 100) / 100;
    },

    /**
	 * 
	 * @param {Number} val 
	 * @returns {String}  3 
	 * @example Currency.format(86234.245) // "86,234.25"
	 */
    format: function (val) {
        var t = Math.round(Math.abs(val) * 100),
            result = Math.floor(val) + '',
            c = (result.length - 1) % 3 + (val < 0 ? 2 : 1);

        return result.substr(0, c) + result.substr(c).replace(/(\d{3})/g, ',$1') + '.' + Math.floor(t / 10) % 10 + t % 10;
    },

    /**
	 * 
	 * @param {Number} val 
	 * @returns {String} 
	 * @example Currency.format(10000000) // ""
	 */
    toChinese: function (val) {

        var digits = '',
            units0 = '',
            units1 = ['', '', '', ''],
            neg = val < 0,
            s = '',
            t;

        val = Math.abs(val);

        // 
        if (val < 0.005) {
            return '';
        }

        // 
        t = Math.round(val * 100) % 100;
        s += t ? (t >= 10 ? digits.charAt(Math.floor(t / 10)) + '' : '') + (t % 10 ? digits.charAt(t % 10) + '' : '') : '';

        // 
        t = Math.floor(val);
        for (var i = 0; i < units0.length && t > 0; i++) {
            var p = '';
            for (var j = 0; j < units1.length && t > 0; j++) {
                p = digits.charAt(t % 10) + units1[j] + p;
                t = Math.floor(t / 10);
            }
            s = (p.replace(/(.)*$/, '') || '') + units0.charAt(i) + s;
        }

        return (neg ? '' : '') + s.replace(/(.)*/, '').replace(/(.)+/g, '');
    }
} 


