<style lang="scss">
    @import './page.scss';
</style>
<template>
    <div class="page">
    <div class="curPage">{{(pageInfo.currentpage - 1) * pageInfo.pageSize + 1}}{{curMaxNum}}{{total}}</div>
    <div class="total"><span></span><input name="inputPage" type="number" class="x-page-input" :value="pageInfo.currentpage"/><span></span> <button type="button" class="btn x-page-goto-page" @click="changePage()"></button></div>
    <ul class="pagination" v-if="total > 0"></ul>
    </div>
</template>
<script>
/*
 *  jqPaginator.js total = 0 
 * param: {'pageInfo': {'pageSize', 'currentPage'},'total'}
 * $emit: 'go-page(num)'
 */
export default({
    props: {
        'pageInfo': {
            default () {
                return {'pageSize': 6, 'currentpage': 1}
            }
        }
        ,'total': {
            default: 100
        }
    }
    ,data () {
        return {'inputNum': ''}
    }
    ,computed: {
        'curMaxNum': function () {
            var _max = this.pageInfo.currentpage * this.pageInfo.pageSize;
            _max = (_max > this.total) ? this.total : _max;
            return _max;
        }
        ,'totalPage': function () {
            return window.Math.ceil(this.total/this.pageInfo.pageSize)
        }
    }
    ,updated() {
        //jqPaginatortotal =0
        if (this.total > 0) {
                this.initPage()                
        }

    }
    ,methods: {
        //
        'changePage': function () {
            let num = $('input[name="inputPage"]').val()
            num = parseInt(num);
            if(isNaN(num)) return;
            //
            if( num > this.totalPage) num = this.totalPage
            //0
            if( num <= 0 ) num = 1
            $('input[name="inputPage"]').val(num)
            this.pageInfo.currentpage = num;
            $(".pagination").jqPaginator('option', {
                currentPage: num
            })
            eventHub.$emit('go-page', num);
        }
        ,'initPage': function () {
            let vm = this;
            let total = parseInt(vm.total);
            let _cPage = 0;
            if (total != 0) {
                _cPage = vm.pageInfo.currentpage
            }
            //
            $.jqPaginator('.pagination',{
                totalCounts: vm.total,
                pageSize: vm.pageInfo.pageSize,
                visiblePages: 5,
                currentPage: _cPage,
                first: '',
                last: '',
                prev: '<li class="prev"><a href="javascript:void(0);"><i class="icon-chevron-left"></i></a></li>',
                next: '<li class="next"><a href="javascript:void(0);"><i class=" icon-chevron-right"></i></a></li>',
                page: '<li class="page"><a href="javascript:void(0);">{{page}}</a></li><li></li>',
                onPageChange: function (num, type) {
                    //vm.changePage(num)
                    eventHub.$emit('go-page', num);
                }
            });
        }
    }
});
</script>


